import React from "react";
import { IconContext } from "react-icons";
import Particles from "react-tsparticles";
import Menu from "./components/Menu";
import IntroCard from "./components/IntroCard";
import About from "./components/About";
import Resume from "./components/Resume";
import Works from "./components/Works";
import Contact from "./components/Contact";
import "./style/app.scss";

const App = () => {
  const [displayedSlide, setDisplayedSlide] = React.useState("about");

  React.useEffect(() => {
    // particlesJS.load("particles-js", "./particles.json");
  }, []);

  return (
    <main className="portfolio " id="particles-js">
      <Particles url="./particles.json" />
      <div className="portfolio__container">
        <IconContext.Provider value={{ className: "portfolio-icon" }}>
          <Menu
            displayedSlide={displayedSlide}
            setDisplayedSlide={setDisplayedSlide}
          />
          <IntroCard />
          <div className="portfolio__slides">
            <About displayedSlide={displayedSlide} />
            <Resume displayedSlide={displayedSlide} />
            <Works displayedSlide={displayedSlide} />
            <Contact displayedSlide={displayedSlide} />
          </div>
        </IconContext.Provider>
      </div>
    </main>
  );
};

export default App;
