import {
  BsFillTelephoneFill,
  MdEmail,
  ImLocation2,
  BsGithub,
  ImLinkedin,
} from "../Icons";

const Contact = ({ displayedSlide }) => {
  return (
    <section
      className={`contacts portfolio__slide ${
        displayedSlide === "contacts" && "active"
      }`}
    >
      <h2 className="contact__header section-header">
        <span className="colored">Get</span> in touch
      </h2>
      <div className="contacts__list">
        <div className="contacts__list-item">
          <BsFillTelephoneFill /> 01022929714
        </div>
        <div className="contacts__list-item">
          <MdEmail /> mosaab.mohamed94@gmail.com
        </div>
        <div className="contacts__list-item">
          <ImLocation2 /> Giza, El Haram St
        </div>

        <div className="contacts__list-item">
          <ImLinkedin />{" "}
          <a
            href="https://linkedin.com/in/mosaab-mohamed-652806bb"
            target="_blank"
            rel="noreferrer"
          >
            https://linkedin.com/in/mosaab-mohamed-652806bb
          </a>
        </div>
        <div className="contacts__list-item">
          <BsGithub />{" "}
          <a
            href="https://github.com/Mosaab-Mohamed"
            target="_blank"
            rel="noreferrer"
          >
            https://github.com/Mosaab-Mohamed
          </a>
        </div>
      </div>
    </section>
  );
};

export default Contact;
