const Works = ({ displayedSlide }) => {
  return (
    <section
      className={`works portfolio__slide ${
        displayedSlide === "works" && "active"
      }`}
    >
      <h2 className="works__header section-header">
        <span className="colored">Recent</span> Works
      </h2>
      <div className="works__list">
        <div className="works__list-item">
          <img
            className="works__list-img"
            src="./images/e-shopy.jpg"
            alt="project"
          />
          <a
            className="works__list-name"
            href="https://mosaab-mohamed.github.io/E-shopy/build/index.html"
            target="_blank"
            rel="noreferrer"
          >
            E-shopy
          </a>
          <p className="works__list-description">
            It's a web application that simulates the e-commerce websites where
            you can watch product, add it to the wishlist or basket...etc
          </p>
          <div className="works__list-technologies">
            Used Languages and technolgies:
            <ul className="works__list-chips">
              <li className="chip">Html</li>
              <li className="chip">Sass</li>
              <li className="chip">ReactJs</li>
              <li className="chip">Redux</li>
            </ul>
          </div>
          <a
            className="works__list-link"
            href="https://mosaab-mohamed.github.io/E-shopy/build/index.html"
            target="_blank"
            rel="noreferrer"
          >
            https://mosaab-mohamed.github.io/E-shopy/build/index.html
          </a>
        </div>

        <div className="works__list-item">
          <img
            className="works__list-img"
            src="./images/imgix.jpg"
            alt="project"
          />
          <a
            className="works__list-name"
            href="https://mosaab-mohamed.github.io/Imgix/build/index.html"
            target="_blank"
            rel="noreferrer"
          >
            Imgix
          </a>

          <p className="works__list-description">
            It's a web application that uses the Unsplash API to retrieve random
            images. It can be used as a portfolio for photographers{" "}
          </p>
          <div className="works__list-technologies">
            Used Languages and technolgies:
            <ul className="works__list-chips">
              <li className="chip">Html</li>
              <li className="chip">Sass</li>
              <li className="chip">ReactJs</li>
              <li className="chip">Swiper js</li>
            </ul>
          </div>
          <a
            className="works__list-link"
            href="https://mosaab-mohamed.github.io/Imgix/build/index.html"
            target="_blank"
            rel="noreferrer"
          >
            https://mosaab-mohamed.github.io/Imgix/build/index.html
          </a>
        </div>
        <div className="works__list-item">
          <img
            className="works__list-img"
            src="./images/codbrush.jpg"
            alt="project"
          />
          <a
            className="works__list-name"
            href="http://codbrush.com/"
            target="_blank"
            rel="noreferrer"
          >
            Codbrush
          </a>

          <p className="works__list-description">
            It's a website of a design agency
          </p>
          <div className="works__list-technologies">
            Used Languages and technolgies:
            <ul className="works__list-chips">
              <li className="chip">Html</li>
              <li className="chip">Sass</li>
              <li className="chip">Javascript</li>
              <li className="chip">Webpack</li>
              <li className="chip">Particlesjs</li>
            </ul>
          </div>
          <a
            className="works__list-link"
            href="http://codbrush.com/"
            target="_blank"
            rel="noreferrer"
          >
            http://codbrush.com
          </a>
        </div>

        <div className="works__list-item">
          <img
            className="works__list-img"
            src="./images/github.jpg"
            alt="project"
          />
          <a
            className="works__list-name"
            href="https://git-users-search.netlify.app/"
            target="_blank"
            rel="noreferrer"
          >
            Github Users Search
          </a>
          <p className="works__list-description">
            It's a web application that uses the Github API to search for GitHub
            users and retrieve their account info such as followers,
            repos...etc. Also, It analyses the most used languages and most
            stared repos...etc
          </p>
          <div className="works__list-technologies">
            Used Languages and technolgies:
            <ul className="works__list-chips">
              <li className="chip">Html</li>
              <li className="chip">Sass</li>
              <li className="chip">ReactJs</li>
              <li className="chip">ContextAPI</li>
              <li className="chip">Github API</li>
              <li className="chip">Auth0</li>
            </ul>
          </div>
          <a
            className="works__list-link"
            href="https://git-users-search.netlify.app/"
            target="_blank"
            rel="noreferrer"
          >
            https://git-users-search.netlify.app
          </a>
        </div>
      </div>
    </section>
  );
};

export default Works;
