import {
  BsFillPersonFill,
  BsFileEarmarkTextFill,
  GiPaintBrush,
  MdMessage,
} from "../Icons";

const Menu = ({ displayedSlide, setDisplayedSlide }) => {
  return (
    <section className="menu ">
      <div
        className={`menu__item ${displayedSlide === "about" && "active"}`}
        onClick={() => setDisplayedSlide("about")}
      >
        <BsFillPersonFill />
        ABOUT
      </div>
      <div
        className={`menu__item ${displayedSlide === "resume" && "active"}`}
        onClick={() => setDisplayedSlide("resume")}
      >
        <BsFileEarmarkTextFill /> RESUME
      </div>
      <div
        className={`menu__item ${displayedSlide === "works" && "active"}`}
        onClick={() => setDisplayedSlide("works")}
      >
        <GiPaintBrush /> WORKS
      </div>

      <div
        className={`menu__item ${displayedSlide === "contacts" && "active"}`}
        onClick={() => setDisplayedSlide("contacts")}
      >
        <MdMessage /> CONTACTS
      </div>
    </section>
  );
};

export default Menu;
